@import "https://fonts.googleapis.com/css2?family=Rubik:wght@500;700&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --very-dark-gray: #2b2b2b;
  --dark-gray: #969696;
}

body {
  background: #f7f7f7;
  font-family: Rubik, sans-serif;
}

input, button {
  border: none;
  padding: .8rem;
  font-size: 1rem;
}

button:hover {
  cursor: pointer;
}

input:focus {
  outline: none;
}

.main-header {
  width: 100vw;
  height: 25vh;
  background-image: url("pattern-bg.93056d32.png");
  background-size: cover;
}

.main-header .search-content-container {
  text-align: center;
  margin: 0 1rem;
  position: relative;
}

.content-container {
  height: 20vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-content-container .title-container {
  color: #fff;
  margin-bottom: 1.4rem;
  font-size: 1.4rem;
  font-weight: 400;
}

.search-content-container .wrap-container {
  display: flex;
}

.search-content-container .input-container input {
  width: 75vw;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-content-container .submit-container button {
  background: var(--very-dark-gray);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.search-content-container .submit-container button img {
  width: 10px;
  height: 10px;
}

.address-info-container, .error-message-container {
  display: none;
}

.address-info-container.active, .error-message-container.active {
  display: block;
}

.address-info-container .info-container, .error-message-container .message-container {
  z-index: 500;
  width: 100%;
  background: #fff;
  border-radius: 10px;
  flex-direction: column;
  padding: 2rem;
  transition: background-color 1s ease-out;
  display: flex;
  position: absolute;
}

.address-info-container .info-wrap-container, .error-message-container .error-wrap-container {
  justify-content: center;
  display: flex;
}

.address-info-container .info-container .content .title {
  color: var(--dark-gray);
  letter-spacing: 1px;
  margin-bottom: .4rem;
  font-size: .7rem;
  font-weight: 900;
}

.address-info-container .info-container .content .title-content {
  margin-bottom: 1.2rem;
  font-size: 1.2rem;
  font-weight: 500;
}

.address-info-container .info-container .content:last-child .title-content {
  margin-bottom: 0;
}

.error-message-container .icon-error {
  color: #f33;
  margin-bottom: 1rem;
  font-size: 4rem;
}

.error-message-container .error-message {
  color: #333;
}

@media only screen and (min-width: 425px) {
  .search-content-container .input-container input, .address-info-container .info-container, .error-message-container .message-container {
    max-width: 425px;
  }
}

@media only screen and (min-width: 1440px) {
  .address-info-container .info-container {
    max-width: 750px;
    flex-direction: row;
    padding: 1rem 2rem 1rem 1.2rem;
  }

  .address-info-container .info-container .content {
    text-align: left;
    border-right: 2px solid #dedede;
    margin-right: 1rem;
    padding-right: 1rem;
  }

  .address-info-container .info-container .content:last-child {
    border: none;
    margin-right: 0;
    padding-right: 0;
  }

  .address-info-container .info-container .content .title-content {
    margin: 0;
    font-size: 1.3rem;
  }
}

#map {
  width: 100vw;
  height: 75vh;
}

.leaflet-popup-content {
  text-align: center;
  font-weight: 900;
}

